.search {
    background-color: white;
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.header__input {
    background-color: #F5F5F5;
    width: 25rem;
    padding: 1rem;
    display: flex;
    align-items: center;
}
.header__search {
    border: none;
    background-color: #F5F5F5;
    font-size: 1.5rem;
   flex: 1;
   border: none;
}
.header__search:focus {
    outline: none;
  }
.search__icon {
    font-size: 2.5rem;
}
.search__icons {
    font-size: 2rem;
    margin-left: 4rem;
}
.search__bottom {
    background-color: #F0F1F3;
    padding: 2rem;
}
.search__header {
    display: flex;
    align-items: center;
}
.menu__icon {
    font-size: 2rem;
    margin-right: 2rem; 
    font-weight: bold;
}
.search__dash {
    font-weight: 800;
}

.search__today {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 2rem;
    color: #C6C6C8;
    margin-left: 0.5rem;
    padding: 0.5rem;
}
.search__today:hover {
    background-color: #FB8B0F;
    border-radius: 1.5rem;
    width: fit-content;
    color: white;
  
}

@media (max-width: 32em) {
.header__input {
    width: 20rem;
    flex-wrap: wrap;
}
.search__icons {
    font-size: 2.5rem;
    margin-left: 3rem;
}

.search__icon {
font-size: 2rem;
}
.header__search {
font-size: 1.5rem;
width: 15rem;
}
.search{
    justify-content: center;
}
}

@media (max-width: 25em) {
    .header__input {
        width: 15rem;
     
    }
    .header__search {
        width: 8rem;
    }
}