.noticeboard {
    font-weight: 500;
    line-height: 1.2;
    letter-spacing: 0.5px;
  }
  .date.orange {
    background-color: #ffe8ce;
    color: #e7a463;
  }
  
  .date.blue {
    color: #6f62b3;
    background-color: #d5d1e9;
  }
  .date.grey {
    color: #727272;
    background-color: #f0eeef;
  }
  .date.yellow {
    color: #fadf8b;
    background-color: #fcf5d1;
  }
  
  .date {
    display: inline-block;
    background-color: #f1eff0;
    padding: 10px 15px;
    border-radius: 16px;
    font-weight: 500;
    margin: 0.8rem 0;
  }
  .description {
    font-weight: 500;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    line-height: 2rem;
  }
  .time {
    color: #858585;
    font-weight: 500;
    font-size: 15px;
    margin-bottom: 10px;
  }
  h2 {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .end{
    margin-top: 4rem;
    background-color: white;
    padding: 2rem;
    border-radius: 2rem;
 margin-left: 2rem;
 margin-right: 1rem;

  }
  .end__endd{
    margin-bottom: 8rem;
  }
.not{
    font-size: 1.8rem;
}


@media (max-width: 63em) {
.end {
    width: 80%;
 
 
}

}

@media (max-width: 28em) {
    .end {
        width: 100%;
     margin-left: 1rem;
     margin-right: 1rem;
}
}