.left {
    padding-left: 2rem;
    padding-right: 2rem;
    width: 100%;
    justify-content: center;
    display: flex;
    flex-direction: column;
}
.left__para {
    font-weight: 800;
    font-size: 1.5rem;
}
.left__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
}
.left__left {
    background-color: #5A45AA;
   height: 15rem;
   width: 48%;
   border-radius: 1rem;
   padding: 2rem;
   display: flex;
   align-items: center;
   justify-content: space-between;
}
.left__right {
    background-color: #3D2B81;
    height: 15rem;
    width: 48%;
    border-radius: 1rem;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.student{
    color: white;
}
.students{
    color: white;
    margin-right: 1rem
}
.student__para {
    color: #9F92CA;
    font-weight: 600;
    margin-bottom: 1rem;
}
.student__bottom {
    display: flex;
    margin-top: 1rem;
}
.student__parag {
    color: #9F92CA;
    font-weight: 600;

}
.student__ava {
    font-size: 10rem;
    color: #BAAEDC;
}
.small__box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.small {
    background-color: white;
    width: 48%;
    margin-top: 2rem;
    height: fit-content;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 2rem;
}
.small__home{
color: #FCC113;
font-size: 5rem;
}
.small__para{
    margin-bottom: 0.5rem;
}
.chart {
    background-color: white;
    border-radius: 2rem;
    padding: 2rem;
    margin-top: 2rem;

}
.fee {
    display: flex;
    margin-top: 2rem;
}
.fee__fee {
    margin-right: 1rem;
  
}
.fee__para {
    margin-bottom: 0.5rem;
    color: #b7b3b3;
}

.fee__line {
    border: 2px solid #8272D7;
    width: 50%;
    margin-top: 0.5rem;
}
.fee__linee {
    border: 2px solid #E08E39;
    width: 50%;
    margin-top: 0.5rem;
}
.left__img {
    width: 100%;
}

@media (max-width: 63em) {
    .left__img {
height: 20rem;
width: 80%;
    }
    .chart {
        width: 80%;
        
    }
    .chart__chart{
        display: flex;
        justify-content: center;
    }
}


@media (max-width: 32em) {
.student > h1 {
    font-size: 1.5rem;
}
.students > h1 {
    font-size: 1.5rem;
}

}

@media (max-width: 28em) {

    .left__left {
      
       width: 100%;
     margin-bottom: 2rem;
    }
    .left__right {
        width: 100%;
    }
    .left__box{
        flex-direction: column;
            }
            .student > h1 {
                font-size: 2rem;
            }
            .students > h1 {
                font-size: 2rem;
            }
            .student__ava {
                font-size: 12rem;

            }
            .chart, .left__img {
                width: 100%;
                
            }
    }