.home {
    display: grid;
    grid-template-columns: 1fr 6fr;
}
.home__menu {
    background-color: #061F3E;
    height: auto;
    color: white;
}
.home__middle {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: 180px 2fr;
    background-color: #F0F1F3;
  
}
.a {
    grid-column: 1/6;
    height: max-content;
    
}


@media (max-width: 63em) {
  .home__left{
    grid-column: 1/4;
    grid-row: 2;
  }
    .cladener {
        grid-column: 1/4;
        margin-right: 1rem;
        grid-row: 3;
    }
    .end__end {
        grid-column: 1/4;    
        grid-row: 4;
       display: flex;
       justify-content: center;
    }
}
