.middle__caleder {
    background-color: white;
    width: 100%;
    height: 37rem;
    margin-top: 4rem;
    border-radius: 2rem;
}
.middle__image{
    background-color: white;
    width: 100%; 
    border-radius: 2rem;
    margin-top: 2rem;
    padding: 2rem;
    height: 38rem;
}
.fee__line0 {
    border: 2px solid #8272D7;
    width: 80%;
    margin-top: 0.5rem;
}
.fee__line1 {
    border: 2px solid #8272D7;
    width: 60%;
    margin-top: 0.5rem;
}
.fee__line2 {
    border: 2px solid #F0CE66;
    width: 60%;
    margin-top: 0.5rem;
}
.expanse {
    margin-bottom: 1rem;
}
.middle__middle {
    display: flex;
}
.bar {
    width: 100%;
}
.mid {
    display: flex;
     justify-content: center;
}

@media (max-width: 63em) {
    .middle__caleder {
width: 80%;
    }
    .middle__image {
        width: 80%;
    
    }
    .bar {
        height: 20rem;
        width: 80%;
    }
}


@media (max-width: 28em) {
    .middle__caleder {
        width: 100%;
            }   .middle__image, .bar{
                width: 100%;
            
            }
            .middle{
                margin-left: 2rem;
                margin-right: 1rem;
            }
        }
        
          